import React from 'react';
import graphQLFetch from './graphQLFetch.js';
import { Link } from 'react-router-dom';
export default class IssueEdit extends React.Component {
	constructor() {
    super();
    this.state = {
    issue: {},
    };
    this.onChange = this.onChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
}
	componentDidMount() {
    this.loadData();
}
	componentDidUpdate(prevProps) {
    const { match: { params: { id: prevId } } } = prevProps;
    const { match: { params: { id } } } = this.props;
    if (id !== prevId) {
      this.loadData();
    }
}
	onChange(event) {
    const { name, value } = event.target;
    this.setState(prevState => ({
    issue: { ...prevState.issue, [name]: value },
}));
}
	handleSubmit(e) {
e.preventDefault();
const { issue } = this.state;
console.log(issue); // eslint-disable-line no-console
}
	async loadData() {
	const query = `query {
    issue(id: 1) {
    id name category price image
}
}`;
	const { match: { params: { id } } } = this.state;
	const { match: { params: { id: propsId } } } = this.props;
    const data = await graphQLFetch(query,{ id });
    if (data) {
		const { issue } = data;
        issue.name = issue.name ? issue.name.toString() : '';
        issue.category = issue.category != null ? issue.category.toString() : '';
        issue.price = issue.price != null ? issue.price : '';
        issue.image = issue.image != null ? issue.image : '';
        this.setState({ issue });
    }else {
      this.setState({ issue: {} });
		}
}
	
	render() {
    const { issue: { id } } = this.state;
    const { match: { params: { id: propsId } } } = this.props;
    if (id == null) {
        if (propsId != null) {
            return <h3>{`Issue with ID ${propsId} not found.`}</h3>;
        }
        return null;
    }
    const { issue: { name, category } } = this.state;
    const { issue: { price, image } } = this.state;
    return (
      <form onSubmit={this.handleSubmit}>
        <h3>{`Editing issue: ${id}`}</h3>
        <table>
          <tbody>
            <tr>
              <td>Name:</td>
              <td>{name.toString()}</td>
            </tr>
            <tr>
              <td>Category:</td>
              <td>
                <select name="category" value={category} onChange={this.onChange}>
                  <option value="Shirts">Shirts</option>
                  <option value="Jeans">Jeans</option>
                  <option value="Jackets">Jackets</option>
                  <option value="Sweaters">Sweaters</option>
                  <option value="Accessories">Accessories</option>
                </select>
              </td>
            </tr>
            <tr>
              <td>Price:</td>
              <td>
                <input
                  name="price"
                  value={price}
                  onChange={this.onChange}
                />
              </td>
            </tr>
            <tr>
              <td>Image:</td>
              <td>
                <input
                  name="image"
                  value={image}
                  onChange={this.onChange}
                />
              </td>
            </tr>
            <tr>
              <td />
              <td><button type="submit">Submit</button></td>
            </tr>
          </tbody>
        </table>
        <Link to={`/edit/${id - 1}`}>Prev</Link>
        {' | '}
        <Link to={`/edit/${id + 1}`}>Next</Link>
      </form>
    );
  }
}